.logoWrap {
	border-radius: 4px;
	pointer-events: none;
	background-color: rgba(26, 188, 156, 0.9);
}

.in-top .logoWrap {
	animation: in-top 300ms ease 0ms 1 forwards;
}

.in-right .logoWrap {
	animation: in-right 300ms ease 0ms 1 forwards;
}

.in-bottom .logoWrap {
	animation: in-bottom 300ms ease 0ms 1 forwards;
}

.in-left .logoWrap {
	animation: in-left 300ms ease 0ms 1 forwards;
}

.out-top .logoWrap {
	animation: out-top 300ms ease 0ms 1 forwards;
}

.out-right .logoWrap {
	animation: out-right 300ms ease 0ms 1 forwards;
}

.out-bottom .logoWrap {
	animation: out-bottom 300ms ease 0ms 1 forwards;
}

.out-left .logoWrap {
	animation: out-left 300ms ease 0ms 1 forwards;
}

@keyframes in-top {
	from {
		top:-100%;
		left:0;
	}
	to {
		top: 0;
		left:0;
	}
}

@keyframes in-right {
	from {
		top:0;
		left:100%;
	}
	to {
		top:0;
		left:0;
	}
}

@keyframes in-bottom {
	from {
		top: -100%;
		left:0;
	}
	to {
		top: 0;
		left:0;
	}
}

@keyframes in-left {
	from {
		top: 0;
		left:-100%;
	}
	to {
		top: 0;
		left:0;
	}
}

@keyframes out-top {
	from {
		top: 0;
		left:0;
	}
	to {
		top: -100%;
		left:0;
	}
}

@keyframes out-right {
	from {
		top:0;
		left:0;
		
	}
	to {
		top:0;
		left:100%;
	
	}
}

@keyframes out-bottom {
	from {
		top:0;
		left:0;
	}
	to {
		top:100%;
		left:0;
	}
}

@keyframes out-left {
	from {
		top:0;
		left:0;
	}
	to {
		top:0;
		left:-100%;
	}
}
